import { formTypes } from "../../types/formTypes"

// **  Initial State
const initialState = {
    formData:{
        base: {},
        adresses:[],
        contacts:[]
    }
}

  
const formReducer = (state = initialState, action) => {
    switch (action.type) {
        case formTypes.setData:
            return {
                ...state
            }

        case formTypes.baseFormChange:
            return {
                ...state
            }

        case formTypes.formRepeaterChange:
    
            const { idGroup, id, camp, value } = action.payload.data
            let newObject = state.formData[idGroup].find(reg => reg.id === id)
            
            newObject
            ? newObject = { ...newObject, [camp] : value}
            : newObject = { id, [camp] : value }
           
            const newGroup = [
                    ...state.formData[idGroup].filter(reg => {
                        return reg.id !== id
                    }),
                    newObject
                ]
         
            return {
                ...state,
                formData:{
                    ...state.formData,
                    [idGroup] : newGroup.sort((item1, item2) => (item1.id - item2.id))
                }  
            }
        case formTypes.formRepeaterAdd:

            return {
                ...state,
                formData:{
                    ...state.formData,
                    [action.payload.idGroup] : [ 
                        ...state.formData[action.payload.idGroup],
                        {id : action.payload.id}
                     ]
                }
            }
        case formTypes.formRepeaterDelete:

            return {
                ...state,
                formData:{
                    ...state.formData,
                    [action.payload.idGroup] : [ ...state.formData[action.payload.idGroup].filter(reg => reg.id !== action.payload.id) ]
                }
            }

        case formTypes.formBaseChange:
            return {
                ...state,
                formData: {
                    ...state.formData,
                    base:{
                        ...state.formData.base,
                        [action.payload.camp]: action.payload.value
                    }
                }
            }

        default:
        return state
    }
}

export default formReducer
  